<template>
  <v-app>
    <!--    <v-img-->
    <!--        :src="require('../../assets/GHim.png')"-->
    <!--        class="center"-->
    <!--    ></v-img>-->
    <v-main>
      <v-container class="fill-height" fluid>
        <v-row align="center" justify="center">
          <v-col cols="12" sm="8" md="8">
            <v-form @submit.prevent="login" id="login-form" autocomplete="off">
              <!--<div class="text-center mb-0">-->
              <!--<img-->
              <!--:src="require('./../../assets/logo.png')"-->
              <!--style="width: 150px"-->
              <!--/>-->
              <!--</div>-->
              <v-card class="pt-3 mx-auto" max-width="780" elevation="15">
                <v-row
                  ><v-col
                    cols="6"
                    sm="6"
                    class="mb-0 py-0"
                    style="background-color: #0984e3"
                  >
                    <v-card-text>
                      <template>
                        <v-img
                          :src="require('../../assets/medGH.png')"
                          class="center"
                        ></v-img>
                      </template>
                    </v-card-text> </v-col
                  ><v-divider vertical class="my-4"></v-divider
                  ><v-col
                    cols="6"
                    sm="6"
                    class="mb-0 py-0"
                    style="margin-top: 35px"
                  >
                    <v-card-text
                      class="text-center body-2 grey--text text--darken-1"
                    >
                      <span
                        class="text-center body-2 grey--text text--darken-1"
                      >
                        {{ $vuetify.lang.t("$vuetify.security.title") }}
                      </span>
                    </v-card-text>
                    <v-card-text class="text-center mb-0">
                      <v-text-field
                        autofocus
                        id="username"
                        :label="
                          $vuetify.lang.t(
                            '$vuetify.security.cols.username.title'
                          )
                        "
                        @blur="$v.username.$touch()"
                        @input="$v.username.$touch()"
                        :error-messages="usernameErrors"
                        name="login"
                        v-model="username"
                        color="#0984e3"
                        autocomplete="off"
                        type="text"
                        class="font-weight-bold"
                        outlined
                        prepend-inner-icon="mdi-account"
                      ></v-text-field>
                      <v-text-field
                        id="password"
                        :label="
                          $vuetify.lang.t(
                            '$vuetify.security.cols.password.title'
                          )
                        "
                        @blur="$v.password.$touch()"
                        @input="$v.password.$touch()"
                        :error-messages="passwordErrors"
                        name="password"
                        prepend-inner-icon="mdi-lock"
                        type="password"
                        outlined
                        v-model="password"
                        color="#0984e3"
                        autocomplete="off"
                        class="font-weight-bold"
                      ></v-text-field>
                      <v-card-text class="text-left mb-0">
                        <router-link
                          style="text-decoration: none; color: #0984e3"
                          class="
                        font-weight-bold
                        body-2
                        text--d text--darken-4
                      "
                          :to="{ name: 'reset' }"
                        >
                          {{
                            $vuetify.lang.t(
                              "$vuetify.security.cols.password.title2"
                            )
                          }}
                        </router-link>
                      </v-card-text>
                      <v-btn
                        color="#0984e3"
                        block
                        elevation="9"
                        type="submit"
                        :loading="loading"
                        dark
                      >
                        {{ $vuetify.lang.t("$vuetify.security.login.title") }}
                      </v-btn>
                    </v-card-text>
                    <v-card-text class="text-right mb-0">
                      <v-tooltip top>
                        <template v-slot:activator="{ on }">
                          <v-btn icon @click="changeLanguage()" v-on="on">
                            {{ $vuetify.lang.current.toUpperCase() }}
                          </v-btn>
                        </template>
                        <span
                          class="
                        text-center
                        font-weight-bold
                        body-2
                        black--text
                        text--d
                        ken-1
                      "
                        >
                          {{ $vuetify.lang.t("$vuetify.btn.change_language") }}
                        </span>
                      </v-tooltip>
                    </v-card-text>
                  </v-col></v-row
                >
              </v-card>
            </v-form>
          </v-col>
        </v-row>
      </v-container>
    </v-main>
  </v-app>
</template>
<script>
import { minLength, required } from "vuelidate/lib/validators";
import { mapGetters } from "vuex";
export default {
  name: "Login",
  data() {
    return {
      username: "",
      password: "",
      errors: [],
      loading: false
    };
  },
  // _.hasIn(errors, 'username')? errors['username'] : usernameErrors"
  created() {
    this.$store.dispatch("showNotification", { statut: false });
  },
  validations: {
    username: {
      required,
      minLength: minLength(2)
    },
    password: {
      required
    }
  },
  methods: {
    changeLanguage() {
      let lang;
      if (this.$vuetify.lang.current === "fr") {
        lang = "en";
      } else {
        lang = "fr";
      }
      window.localStorage.setItem("lang", lang);
      this.$vuetify.lang.current = lang;
    },
    login() {
      // console.log(window.location.origin + '/api/v1'+window.location.pathname);
      let self = this;
      let badCredential = this.$vuetify.lang.t(
        "$vuetify.security.form.bad_credentials"
      );
      self.$v.$touch();
      if (!self.$v.$invalid) {
        this.loading = true;
        const credentials = {
          username: this.username,
          password: this.password
        };
        //console.log("$auth: ", this.$auth);
        const redirect = this.$auth.redirect();
        this.$auth
          .login({
            data: credentials,
            redirect: {
              name:
                redirect && redirect.from !== undefined
                  ? redirect.from.name
                  : "dashboard"
            },
            fetchUser: true
          })
          .then(() => {
            //console.log("user", res);
          })
          .catch(error => {
            if (error.response.status === 401) {
              const datas = error.response.data.detail;
              //console.log("datas", datas);
              self.errors["username"] = datas;
              self.$store.dispatch("showNotification", {
                statut: true,
                text: datas,
                color: "#B71C1C"
              });
              //self.$forceUpdate();
              //console.log("eerreur");
            } else {
              self.$store.dispatch("showNotification", {
                statut: true,
                text: badCredential,
                color: "#B71C1C"
              });
            }
            self.loading = false;
          })
          .finally(() => {
            self.loading = false;
          });
      }
    }
  },
  computed: {
    ...mapGetters(["notification"]),
    usernameErrors() {
      const errors = [];
      if (!this.$v.username.$dirty) return errors;
      !this.$v.username.required &&
        errors.push(
          this.$vuetify.lang.t("$vuetify.users.cols.username.required")
        );
      return errors;
    },
    passwordErrors() {
      const errors = [];
      if (!this.$v.password.$dirty) return errors;
      !this.$v.password.required &&
        errors.push(
          this.$vuetify.lang.t("$vuetify.users.cols.password.required")
        );
      return errors;
    }
  }
};
</script>
<style scoped>
.fill-height {
  background-image: url(../../assets/GHim.png);
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
}
</style>
